<template>
  <div id="sidebar">
    <div class="component-item">
      <pan-thumb width="100px" height="100px" image="https://wpimg.wallstcn.com/577965b9-bb9e-4e02-9f0c-095b41417191">
      </pan-thumb>
    </div>
		<ul>
			<li v-for="(item,index) in sidebarList" class="list-item">
				<router-link :to="item.url">{{item.content}}</router-link>
			</li>
		</ul>
	</div>
</template>
<script>
import waves from '@/directive/waves/index.js' // 水波纹指令
import PanThumb from '@/components/PanThumb'
export default {
	data() {
		return {
			sidebarList: [],
			adminSidebar: [
				{content: '注意事项', url: '/matters'},
				{content: '出租信息管理', url: '/renthouse'},
				{content: '出售信息管理', url: '/sellhouse'},
				{content: '求租信息管理', url: '/solicithouse'},
				{content: '购买信息管理', url: '/buyhouse'},
				{content: '会员信息管理', url: '/userlist'},
				{content: '留言板管理', url: '/msgborlist'}
			]
		}
	},
  directives: {
    waves
  },
  components: {
    PanThumb,
  },
	created() {
			this.sidebarList = this.sidebarList.concat(this.adminSidebar)
	},
	methods: {
	}
}
</script>

<style scoped>
#sidebar{
	position: fixed;
	padding-top: 1px;
	width: 180px;
	height: 100%;
	z-index: 30;
	border-right: 1px solid #fff;
}
.list-item{
	height: 50px;
	line-height: 50px;
	text-align: center;
	font-family:"华文彩云";
  font-size: large;
}
.list-item a.active{
	display: block;
	background: rgba(7,17,27,1);
}
.component-item{
  min-height: 100px;
  padding-top: -50px;
}
</style>



